import React from 'react';
import Flip from 'react-reveal/Flip';
function Cards(props ){
return(
<>
<Flip left>
<div className="card m-3 text-center" style={{width:'18rem'}}>
  <img src={props.imgsrc} className=" card-img-bottom" alt="..."/>
  <div className="card-body">
    <h5 className="card-title">{props.title}</h5>
    <p className=" body-content-font text-justify ">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
    <a href="#" className="button_slide slide_right text-decoration-none ">Explore</a>
  </div>
</div>
</Flip>


</>

);

}
export default Cards;