import React from 'react';
function Introduction()
{
const introductory_text='TRAVEL'
const current_date= new Date().toLocaleDateString();
const current_time= new Date().toLocaleTimeString();
const img1_link="https://picsum.photos/200/300?grayscale&random=1";
const img2_link="https://picsum.photos/200/300?grayscale&random=2";
const img3_link="https://picsum.photos/200/300?grayscale&random=3";
const img4_link="https://picsum.photos/200/300?grayscale&random=4";
const img5_link="https://picsum.photos/200/300?grayscale&random=5";
const img6_link="https://picsum.photos/200/300?grayscale&random=6";
    return(
        <React.Fragment>
    
        {/* row */}
      <div  className='row text-center m-4'> 
           <h1>{` ${introductory_text}`}</h1>
      </div>
      <div className='row'style={{minHeight:'100vh'}}>
        <div className='text-center'>
        <p className='d-none'>{`Current Date is ${current_date}` }</p>
        <p className='d-none'>{`And Current Time is ${current_time}` }</p>
    
        <img className='mb-4  hover01' src={img1_link} alt="lorempic-1" />
        <img className='mb-4 hover01' src={img2_link} alt="lorempic-2" /> 
        <img className='mb-4 hover01' src={img3_link} alt="lorempic-3" /> 
        <img className='mb-4 hover01' src={img4_link} alt="lorempic-4" />
        <img className='mb-4 hover01' src={img5_link} alt="lorempic-5" />
        <img className='mb-4 hover01' src={img6_link} alt="lorempic-6" />
         <br />
        <p className='body-content-font p-lg-5 text-justify'>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Numquam molestias reprehenderit quis temporibus autem! Voluptatibus ipsa perspiciatis
          eos quaerat sequi sint architecto id quia hic eius. Qui, praesentium sequi quia enim in officiis veritatis iure cumque tempore nobis magnam ab!
        </p>
      
        <button className=' button-font button_slide slide_right' type='button'>Read more</button>
        </div>
      </div>
      
      
  </React.Fragment>
    );
} 
export default Introduction;