import React from 'react';
import Introduction from './components/Introduction';
import Navbar from './components/Navbar_component';
import Cards from './components/Cards';
import Destination_data from './components/Destinationdata';
import Footer from './components/Footer';
import DestinationsToExplore from './components/DestinationsToExplore';
function App(){
return(
    <>
      <Navbar/>  
    <Introduction/>
    <section style={{minHeight:'100vh'}} id='destinations' className='bg-dark'>
      <DestinationsToExplore/>

    <div className=' row  justify-content-lg-center justify-content-evenly'>
            {
              Destination_data.map((val)=>{

                return(
                  <Cards 
                        title={val.destination_name}
                        imgsrc={val.imgsrc} 
                  />
                );

                })
             } 
    </div>

    </section>
    {/* <Footer/> */}
    <Footer/>
   </>
  
    );

}
export default App;